.side-bar{
  background: rgb(34, 34, 34);
  height: 100vh;
  width: 55px;
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  /* justify-content: space-between; */
  top: 0;
  left: 0; 
  -webkit-transition: 0.2s; 
  -o-transition: 0.2s; 
  transition: 0.2s;
  z-index: 100;
}

.side-bar:hover{
  width: 190px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}


.side-bar__menu-list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}


.side-bar__menu-list_content{
  overflow: hidden;
  font-size: 17px;
  font-weight: 600;
  display: inline-block;
  margin: 10px 0 10px 20px;
  color: rgb(192, 192, 192);
  list-style-type: none;
  cursor: pointer;
}


.leaders{
  margin: 0 0 49px 60px;
}


.side-bar__menu_logo{
  width: 36px;
  height: 36px;
  position: absolute; 
  top: 0;
  left: 8px; 
  background: url('./icons/logo.png') center/100% 100% no-repeat;
}



.icon{
  /* background: blue; */
  position: absolute;
}


.math{
  top: 46px;
  left: 12px;
}


.question{
  top: 126px;
  left: 12px;
}


.forum{
  top: 166px;
  left: 12px;
}


.contact{
  top: 86px;
  left: 12px;
}

.cup{
  top: 164px;
  left: 12px;
}

@media (max-width: 1100px){
  .side-bar{
    display: none;      
  }
  .burger{
    width: 50px;
    height: 50px;
    position: absolute;
    top: 20px;
    left: 20px;
    /* background: blue; */
  }
  .exit{
    width: 70px;
    height: 70px;
    /* background: blue; */
  }
  .cross-one{
    background:#ccc;
    width: 30px;
    height: 3px;
    border-radius: 10px;
    margin: 5px 0;
    transform: rotate(45deg);
    position: absolute;
    top: 30px;
    left: 20px;
  }
  .cross-two{
    background:#ccc;
    width: 30px;
    height: 3px;
    border-radius: 10px;
    transform: rotate(-45deg);
    margin: 5px 0;
    position: absolute;
    top: 30px;
    left: 20px;
  }
  .burger>div{
    background:#ccc;
    width: 30px;
    height: 3px;
    border-radius: 10px;
    margin: 5px 0;
  }
  .side-bar.active{
    display: block;
    width: 100vw;
  }
  .side-bar__menu{
    margin: 70px auto 0;
    width: 150px;
  }
  
}

